import React from "react";
import Slider from "@mui/material/Slider";

import { useDebounce } from "../../hooks";

import "./priceSlider.scss";

const PriceSlider = ({ value, setValue }) => {
	const [localValue, setLocalValue] = React.useState(value.selectedRange || value.availableRange);
	const debounceLocalValue = useDebounce(localValue, 500);

	// Setting new priceSliderValue on CataloguePage with a delay
	React.useEffect(() => {
		setValue((prevState) => ({
			...prevState,
			selectedRange: debounceLocalValue,
		}));
	}, [JSON.stringify(debounceLocalValue)]);

	const { availableRange, selectedRange } = value;

	const handleChange = (event, newValue) => {
		setLocalValue(newValue);
	};

	const valuetext = (value) => {
		return `${value} US dollars`;
	};

	const onInputChange = (event, inputType, value) => {
		event.preventDefault();

		if (!value.match(/\D+/gi)) {
			switch (inputType) {
				case "minPrice": {
					setLocalValue((prevState) => [
						+value > availableRange[1] ? availableRange[0] : +value,
						prevState[1],
					]);
					break;
				}
				case "maxPrice": {
					setLocalValue((prevState) => [
						prevState[0],
						+value > availableRange[1] ? availableRange[1] : +value,
					]);
					break;
				}
				default: {
					break;
				}
			}
		}
	};

	return (
		<div className="price-slider">
			<div className="price-slider__body">
				<div className="price-slider__inputs">
					<div className="price-slider__item">
						<div className="price-slider__label">from</div>
						<input
							type="text"
							inputMode="numeric"
							className="price-slider__input price-slider__input--min"
							name="Price"
							aria-label="input a minimum price"
							value={localValue[0]}
							onChange={(e) => {
								onInputChange(e, "minPrice", e.target.value);
							}}
						/>
					</div>
					<div className="price-slider__item">
						<div className="price-slider__label">to</div>
						<input
							type="text"
							inputMode="numeric"
							className="price-slider__input price-slider__input--max"
							name="Price"
							aria-label="input a maximum price"
							value={localValue[1]}
							onChange={(e) => {
								onInputChange(e, "maxPrice", e.target.value);
							}}
						/>
					</div>
				</div>
				<div className="price-slider__slider">
					<Slider
						getAriaLabel={() => "Products price range"}
						value={localValue}
						onChange={handleChange}
						valueLabelDisplay="off"
						getAriaValueText={valuetext}
						disableSwap
						min={availableRange[0]}
						max={availableRange[1]}
					/>
				</div>
			</div>
		</div>
	);
};

export default PriceSlider;
