import { Link } from "react-router-dom";
import Rating from "@mui/material/Rating";
import { useDispatch, useSelector } from "react-redux";
import classNames from "classnames";
import { useInView } from "react-intersection-observer";

import { addProduct, removeProduct } from "../../redux/slices/orderingSlice";
import {
	addToFavorites,
	removeFromFavorites,
	addToCompare,
	removeFromCompare,
} from "../../redux/slices/productsStatusSlice";
import ProductCardSkeleton from "../ProductCardSkeleton/ProductCardSkeleton";

import "./productCard.scss";

const ProductCard = (props) => {
	const { id, productId, title, category, price, discount, labels, images, characteristics, reviews } = props;

	const dispatch = useDispatch();

	const isProductInCart = useSelector((state) =>
		state.ordering.productsInCart.some((product) => product.productId === productId)
	);
	const isProductInFavorites = useSelector((state) =>
		state.productsStatus.favorites.items.some((item) => item === productId)
	);
	const isProductInCompare = useSelector((state) =>
		state.productsStatus.inCompare.items.some((item) => item === productId)
	);

	const { ref, inView } = useInView({
		threshold: 0.1,
		triggerOnce: true,
	});

	const productClassnames = classNames("product-article", {
		"in-cart": isProductInCart,
		"in-favorites": isProductInFavorites,
		"in-compare": isProductInCompare,
	});

	const toggleProductToCart = () => {
		if (isProductInCart) {
			dispatch(removeProduct({ productId }));
		} else {
			dispatch(addProduct({ ...props, itemsInCart: 1 }));
		}
	};

	const toggleProductToFavorites = () => {
		if (isProductInFavorites) {
			dispatch(removeFromFavorites({ productId }));
		} else {
			dispatch(addToFavorites({ productId }));
		}
	};

	const toggleProductToCompare = () => {
		if (isProductInCompare) {
			dispatch(removeFromCompare({ productId }));
		} else {
			dispatch(addToCompare({ productId }));
		}
	};

	const onClickFastBuy = () => {
		if (!isProductInCart) {
			dispatch(addProduct({ ...props, itemsInCart: 1 }));
		}
	};

	return (
		<li className="product" ref={ref}>
			{inView ? (
				<article className={productClassnames}>
					<div className="product__labels">
						{labels && labels.includes("Novelty") ? (
							<div className="product__label product__label--new">Novelty</div>
						) : null}
						{labels && labels.includes("Hit sales") ? (
							<div className="product__label product__label--top-sale">Hit sales</div>
						) : null}
					</div>
					<Link
						className="product__link product__link--photo"
						to={`/products/${productId}`}
						aria-label={`go to page of ${title}`}
					>
						<picture>
							<source className="product__img" srcSet={images[0].webp} type="image/webp" />
							<img className="product__img" src={images[0].src} alt={title} />
						</picture>
					</Link>
					<div className="product__category">{category}</div>
					<div className="product__title">
						<Link
							className="product__link product__link--name"
							to={`/products/${productId}`}
							aria-label={`go to page of ${title}`}
						>
							<p className="product__name">{title}</p>
						</Link>
					</div>
					<div className="product__user-evaluation">
						<div className="product__rating rating">
							<Rating
								className="rating__body"
								name="hover-feedback"
								value={reviews.rating ? +reviews.rating : 0}
								precision={0.1}
								readOnly
								size="large"
							/>
							{reviews.rating && <div className="rating__value">{(+reviews.rating).toFixed(2)}</div>}
						</div>
						<Link
							className="product__reviews"
							to={`/products/${productId}/reviews`}
							aria-label={`go to page of reviews for ${title}`}
						>
							<svg
								width="24"
								height="24"
								viewBox="0 0 24 24"
								fill="none"
								xmlns="http://www.w3.org/2000/svg"
							>
								<path
									d="M21 15C21 15.5304 20.7893 16.0391 20.4142 16.4142C20.0391 16.7893 19.5304 17 19 17H7L3 21V5C3 4.46957 3.21071 3.96086 3.58579 3.58579C3.96086 3.21071 4.46957 3 5 3H19C19.5304 3 20.0391 3.21071 20.4142 3.58579C20.7893 3.96086 21 4.46957 21 5V15Z"
									stroke="currentColor"
									strokeWidth="2"
									strokeLinecap="round"
									strokeLinejoin="round"
								/>
							</svg>
							<span>({reviews.count})</span>
						</Link>
					</div>
					<div className="product__content">
						<div className="product__prices">
							{price.old ? <p className="product__old-price">{price.old}</p> : null}
							<p className="product__price">{price.current}</p>
							{discount ? (
								<p className="product__discount">
									<span className="product__discount-percent">-{discount.percent}%</span> — ${" "}
									{discount.amount}
								</p>
							) : null}
						</div>
						<div className="product__actions">
							<button
								className="product__btn product__btn--favorite"
								onClick={toggleProductToFavorites}
								aria-label="add the product to list 'Favorites'"
							></button>
							<button
								className="product__btn product__btn--compare"
								onClick={toggleProductToCompare}
								aria-label="add the product to list 'Comparement'"
							></button>
						</div>
					</div>
					<div className="product__purchase">
						<Link
							className="product__buy-btn"
							to="/ordering"
							onClick={onClickFastBuy}
							aria-label="add the product to a cart and go to the Ordering page"
						>
							Buy in one click
						</Link>
						<button
							className="product__cart-btn"
							onClick={toggleProductToCart}
							aria-label="add the product to Cart"
						/>
					</div>
				</article>
			) : (
				<ProductCardSkeleton />
			)}
		</li>
	);
};

export default ProductCard;
