import { CircularProgress } from '@mui/material';

import FilterItemBody from '../../components/filterItemBody/FilterItemBody';

const FilterCatalogueFormSkeleton = () => {
    return (
        <div className="filter-catalogue__form">
            <FilterItemBody title="Price, $" expanded disabled>
                <div style={{display:'flex', justifyContent: 'center'}}>
                    <CircularProgress/>
                </div>
            </FilterItemBody>
            <FilterItemBody title="Lightning" expanded disabled>
                <div style={{display:'flex', justifyContent: 'center'}}>
                    <CircularProgress/>
                </div>
            </FilterItemBody>
            <FilterItemBody title="Engine power, W" disabled>
                <div style={{display:'flex', justifyContent: 'center'}}>
                    <CircularProgress/>
                </div>
            </FilterItemBody>
            <div className="filter-catalogue__item filter-item">
                <button className="filter-item__btn" type="submit" disabled>Apply</button>
            </div>
        </div>
    );
}

export default FilterCatalogueFormSkeleton;