import './productCardSkeleton.scss';

const ProductCardSkeleton = (props) => {
    return (
        <div className="product-skeleton">
            <div className="product-skeleton__labels"></div>
            <div className="product-skeleton__img"></div>
            <div className="product-skeleton__category"></div>
            <div className="product-skeleton__title"></div>
            <div className="product-skeleton__user-evaluation">
                <div className="product-skeleton__rating"></div>
                <div className="product-skeleton__reviews"></div>
            </div>
            <div className="product-skeleton__content">
                <div className="product-skeleton__prices"></div>
                <div className="product-skeleton__actions"></div>
            </div>
            <div className="product-skeleton__purchase">
                <div className="product-skeleton__buy-btn"></div>
                <div className="product-skeleton__cart-btn"></div>
            </div>
        </div>
    )
}

export default ProductCardSkeleton;