import ProductCardSkeleton from "../ProductCardSkeleton/ProductCardSkeleton";

const ProductsSectionSkeleton = ({items}) => {
    switch (items) {
        case 3: {
            return (
                <>
                    <ProductCardSkeleton/>
                    <ProductCardSkeleton/>
                    <ProductCardSkeleton/>
                </>
            );
        }
        default: {
            return (
                <>
                    <ProductCardSkeleton/>
                    <ProductCardSkeleton/>
                    <ProductCardSkeleton/>
                    <ProductCardSkeleton/>
                </>
            )
        }
    }
}

export default ProductsSectionSkeleton;