import './productsSectionError.scss';
import errorIcon from '../../assets/images/icons/errorIcon.svg';

const ProductsSectionError = () => {
    return (
        <div className="products__error">
            <img src={errorIcon} alt="error happened when loading product data" aria-hidden />
            <p>Sorry! Something went wrong and products haven't been loaded. <br /> Please, try again later.</p>
        </div>
    );
};

export default ProductsSectionError;