import React from "react";

import "./filterItemBody.scss";

const FilterItemBody = ({ title, children, expanded, disabled }) => {
	const [isOpened, setIsOpened] = React.useState(expanded);
	const [bodyHeight, setBodyHeight] = React.useState("fit-content");
	const filterBodyRef = React.useRef();

	React.useEffect(() => {
		setBodyHeight(filterBodyRef.current.scrollHeight + 40);
	}, []);

	const itemClassNames = "filter-catalogue__item filter-item";

	const toggleFilterItem = () => {
		setIsOpened((prevState) => !prevState);
	};

	const handleKeydown = (event) => {
		if (event && (event.code === "Enter" || event.code === "Space")) {
			event.preventDefault();
			toggleFilterItem();
		}
	};

	return (
		<div
			className={isOpened ? `${itemClassNames} opened` : itemClassNames}
			onClick={!disabled ? toggleFilterItem : null}
		>
			<div
				className={disabled ? "filter-item__header disabled" : "filter-item__header"}
				tabIndex={0}
				role="button"
				onKeyDown={handleKeydown}
			>
				{title}
			</div>
			<div
				className="filter-item__body"
				onClick={(e) => e.stopPropagation()}
				ref={filterBodyRef}
				style={{ maxHeight: isOpened ? bodyHeight : 0 }}
			>
				{children}
			</div>
		</div>
	);
};

export default FilterItemBody;
