import React from 'react';

import './activeFilterItemLabel.scss';

const ActiveFilterItemLabel = ({title, checkedItems, unit, type, onReset}) => {

    let label = '';

    switch (type) {
        case "range": {
            if (unit === "$") {
                label = `${title}: from ${unit}${Math.min(...checkedItems)} to ${unit}${Math.max(...checkedItems)}`;
            } else {
                if (checkedItems.length === 1) {
                    label = `${title}: ${checkedItems[0]} ${unit}`;
                } else {
                    label = `${title}: from ${Math.min(...checkedItems)} ${unit} to ${Math.max(...checkedItems)} ${unit}`;
                }
            }
            break;
        }
        case "no-range": {
            label = `${title}: ${checkedItems.join(', ')}`;
            break;
        }
        default: {
            break;
        }
    }

    return (
        <li className="header-content-catalogue__item">
            <span>{label}</span>
            <button className="header-content-catalogue__item-btn" onClick={onReset}>
                <svg viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fillRule="evenodd" clipRule="evenodd" d="M17.7071 0.292893C18.0976 0.683417 18.0976 1.31658 17.7071 1.70711L1.70711 17.7071C1.31658 18.0976 0.683417 18.0976 0.292893 17.7071C-0.0976311 17.3166 -0.0976311 16.6834 0.292893 16.2929L16.2929 0.292893C16.6834 -0.0976311 17.3166 -0.0976311 17.7071 0.292893Z" fill="currentColor"/>
                    <path fillRule="evenodd" clipRule="evenodd" d="M0.292893 0.292893C0.683417 -0.0976311 1.31658 -0.0976311 1.70711 0.292893L17.7071 16.2929C18.0976 16.6834 18.0976 17.3166 17.7071 17.7071C17.3166 18.0976 16.6834 18.0976 16.2929 17.7071L0.292893 1.70711C-0.0976311 1.31658 -0.0976311 0.683417 0.292893 0.292893Z" fill="currentColor"/>
                </svg>
            </button>
        </li>
    );
}

export default ActiveFilterItemLabel;