import React from 'react';

import { scrollLockContext } from '../../context';

import './select.scss';

const Select = ({placeholder, selectedValue, options, searchModule = false, updateSearchParams, setSelectedSortOption, productsPerPage, checkedFilters, loadNewPage}) => {
    
    const [isOpened, setIsOpened] = React.useState(false);
    const [selectedOption, setSelectedOption] = React.useState(selectedValue || null);
    const [searchValue, setSearchValue] = React.useState('');
    const [filteredOptions, setFilteredOptions] = React.useState(options);
    const {toggleScrollLock} = React.useContext(scrollLockContext);

    React.useEffect(() => {
        if (isOpened) {
            window.addEventListener('click', onClickOutside);

            return () => {
                window.removeEventListener('click', onClickOutside);
            }
        }
    }, [isOpened])

    const selectBodyRef = React.useRef();
    const selectInputRef = React.useRef();
    const selectSearchRef = React.useRef();
    const selectedOptionRef = React.useRef();
    const firstOptionRef = React.useRef();

    const toggleSelect = () => {
        setIsOpened(prevState => !prevState);
        setSearchValue('');
        setFilteredOptions(options);
        toggleScrollLock();
    }

    const chooseOption = (value) => {
        setSelectedSortOption(value);
        setSelectedOption(value);
       
        if (value === 'By rating decrease') {
            updateSearchParams();
        } else {
            updateSearchParams(value);
        }


        loadNewPage(1, productsPerPage, checkedFilters, value);
        setIsOpened(false);
        toggleScrollLock();
    }

    const onSelectInputKeyDown = (event) => {
        if (event.code === 'Space' || event.code === 'Enter') {
            toggleSelect();
        }

        if (isOpened && event.target.matches('.select__input')) {
            if (event.code === 'ArrowDown') {
                if (selectedOptionRef.current) {
                    selectedOptionRef.current.focus();
                }

                if (searchModule) {
                    selectSearchRef.current.focus();
                }
            }
            if (event.code === 'Escape' || event.code === 'Tab') {
                setIsOpened(false);
                toggleScrollLock();
            }
        }
    }

    const onOptionKeyDown = (event, option) => {
        if (isOpened && event.target.matches('.select__item')) {
            let currentOptionIndex = filteredOptions.findIndex(item => item === option);
            if (event.code === 'ArrowDown') {
                
                if (currentOptionIndex < filteredOptions.length - 1) {
                    const nextOptionIndex = currentOptionIndex + 1;
                    event.target.nextElementSibling.focus();
                    setSelectedOption(filteredOptions[nextOptionIndex]);
                } 
            }
            if (event.code === 'ArrowUp') {
                
                if (currentOptionIndex > 0) {
                    const prevOptionIndex = currentOptionIndex - 1;
                    event.target.previousElementSibling.focus();
                    setSelectedOption(filteredOptions[prevOptionIndex]);
                } 
            }
            if (event.code === 'Enter' || event.code === 'Escape') {
                selectInputRef.current.focus();
                chooseOption(event.target.textContent);
            }
        }
    }

    const onClickOutside = (event) => {
        if (event.target.closest('.select') !== selectBodyRef.current) {
            setIsOpened(false);
            toggleScrollLock();
        }
    }

    const onSearchChange = (value) => {
        setSearchValue(value);
        const filteredOptionsList = options.filter(option => (option.toLowerCase().search(value.toLowerCase())) !== -1);
        if (value) {
            setFilteredOptions(filteredOptionsList);
        } else {
            setFilteredOptions(options);
        }
    }

    const onSearchKeyDown = (event) => {
        if (isOpened && event.target.matches('.select__search')) {
            if (event.code === 'ArrowDown') {
                if (firstOptionRef.current) {
                    firstOptionRef.current.focus();
                    setSelectedOption(firstOptionRef.current.textContent);
                } else if (selectedOptionRef.current) {
                    selectedOptionRef.current.focus();
                }
            }
            if (event.code === 'Escape') {
                setIsOpened(false);
                toggleScrollLock();
                selectInputRef.current.focus();
            }
        }
    }

    return (
        <div
            className={isOpened ? 'select opened' : 'select'}
            id="someSelectUniqueId"
            ref={selectBodyRef}>
            <div
                className="select__input"
                onClick={toggleSelect}
                onKeyDown={onSelectInputKeyDown}
                aria-label="Sort by option"
                aria-labelledby="someSelectUniqueId"
                role="combobox"
                aria-expanded={isOpened}
                aria-controls="someSelectUniqueDropdownList"
                tabIndex="0"
                ref={selectInputRef}>
                <span>{selectedOption || placeholder}</span>
            </div>
            <div
                className="select__dropdown"
                aria-expanded={isOpened}
                aria-labelledby="someSelectUniqueId"
                aria-hidden={!isOpened}>
                {searchModule 
                    && <input
                            className="select__search"
                            type="text"
                            placeholder="Search"
                            onChange={(e) => onSearchChange(e.target.value)}
                            onKeyDown={onSearchKeyDown}
                            value={searchValue}
                            ref={selectSearchRef}
                            tabIndex={-1}
                            aria-labelledby="someSelectUniqueId"/>
                }
                <ul className="select__list" role="listbox" id="someSelectUniqueDropdownList">
                    {filteredOptions.length > 0
                        ? filteredOptions.map((item, i) => {
                            return <li
                                        className={item === selectedOption ? 'select__item selected' : 'select__item'}
                                        key={item}
                                        onClick={() => chooseOption(item)}
                                        onKeyDown={(e) => onOptionKeyDown(e, item)}
                                        role="option"
                                        ref={(item === selectedOption && selectedOptionRef) || (i === 0 && firstOptionRef) || null}
                                        aria-selected={item === selectedOption}
                                        tabIndex={-1}>
                                        {item}
                                    </li>
                        })
                        : <p className="select__no-match">No match</p>
                    }
                </ul>
            </div>
        </div>
    )
};

export default Select;