import React from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

import ActiveFilterItemLabel from '../activeFilterItemLabel/ActiveFilterItemLabel';

import './activeFiltersList.scss';

const ActiveFiltersList = ({checkedFilters, animationSpeed, resetFilterList, resetSelectedFilter}) => {
    return (
        <div className="header-content-catalogue__body">
            <TransitionGroup
                className="header-content-catalogue__list"
                component={'ul'}
            >
                {checkedFilters.map(item => {
                    if (item.checkedItems.length) {
                        return (
                            <CSSTransition
                                key={item.title}
                                timeout={animationSpeed}
                                classNames="header-content-catalogue__item"
                            >
                                <ActiveFilterItemLabel onReset={() => resetSelectedFilter(item.title)} {...item}/>
                                
                            </CSSTransition>
                        )
                    }
                })}
            </TransitionGroup>
            {checkedFilters.some(filter => filter.checkedItems.length !== 0) && (
                    <button
                        className="header-content-catalogue__btn-clear"
                        onClick={resetFilterList}
                    >
                        Reset filters
                    </button>
                )
            }
        </div>
    );
}

export default ActiveFiltersList;