import React from "react";
import { Link, useSearchParams } from "react-router-dom";
import { Pagination, PaginationItem } from "@mui/material";
import { Helmet } from "react-helmet";

import { useGoogleSheetsAPI } from "../../hooks";
import { scrollLockContext } from "../../context";
import ProductCard from "../../components/ProductCard/ProductCard";
import ProductsSectionSkeleton from "../../components/ProductsSectionSkeleton/ProductsSectionSkeleton";
import ProductsSectionError from "../../components/productsSectionError/ProductsSectionError";
import ActiveFiltersList from "../../components/activeFiltersList/ActiveFiltersList";
import Select from "../../components/select/Select";
import FilterCatalogueFormSkeleton from "../../components/filterCatalogueFormSkeleton/FilterCatalogueFormSkeleton";
import FilterItemBody from "../../components/filterItemBody/FilterItemBody";
import FilterItemCharacteristicsBox from "../../components/filterItemCharacteristicsBox/FilterItemCharacteristicsBox";
import PriceSlider from "../../components/priceSlider/PriceSlider";

import "./cataloguePage.scss";
import Backdrop from "../../components/Backdrop/Backdrop";

const CataloguePage = (props) => {
	const [searchParams, setSearchParams] = useSearchParams();
	const [products, setProducts] = React.useState([]);
	const [availableCharacteristics, setAvailableCharacteristics] = React.useState([]);
	const [isFirstLoad, setFirstLoad] = React.useState(true);
	const { getProducts, getPagesQuantity, getExistingCharacteristics, getFilteredData, loading, error, setLoading } =
		useGoogleSheetsAPI();
	const { unlockScroll, lockScroll } = React.useContext(scrollLockContext);

	const [selectedSortOption, setSelectedSortOption] = React.useState(
		searchParams.get("sortBy") || "By rating decrease"
	);

	const [pages, setPages] = React.useState({
		currentPageNumber: parseInt(searchParams.get("page")) || 1,
		productsPerPage: 6,
		availablePagesQuantity: null,
	});
	const [isFilterAsideOpened, setFilterAsideOpened] = React.useState(false);
	const [isAppliedFilters, setAppliedFilters] = React.useState(!![...searchParams].length);
	const [checkedFilters, setCheckedFilters] = React.useState([
		{
			title: "Price",
			checkedItems: [],
			type: "range",
			unit: "$",
		},
		{
			title: "Engine power",
			checkedItems: [],
			type: "range",
			unit: "W",
		},
		{
			title: "Maximum speed",
			checkedItems: [],
			type: "range",
			unit: "km/h",
		},
		{
			title: "Lightning",
			checkedItems: [],
			type: "no-range",
		},
		{
			title: "Weight",
			checkedItems: [],
			type: "range",
			unit: "kg",
		},
	]);

	const [isActiveFiltersListVisible, setActiveFiltersListVisible] = React.useState(false);
	const filterCatalogueFormRef = React.useRef();
	const [priceSliderValues, setPriceSliderValues] = React.useState({
		availableRange: null,
		selectedRange: null,
	});

	React.useEffect(() => {
		onFirstLoad();
	}, []);

	const getTotalCountOfPages = async () => {
		const pagesQuantity = await getPagesQuantity(pages.productsPerPage);

		if (pagesQuantity) {
			setPages((prev) => ({
				...prev,
				availablePagesQuantity: pagesQuantity,
			}));
		}
	};

	const getAvailableCharacteristics = async () => {
		let availableCharacteristics;

		if (sessionStorage.getItem("AVAILABLE_CHARACTERISTICS")) {
			availableCharacteristics = JSON.parse(sessionStorage.getItem("AVAILABLE_CHARACTERISTICS"));
		} else {
			availableCharacteristics = await getExistingCharacteristics();
			sessionStorage.setItem("AVAILABLE_CHARACTERISTICS", JSON.stringify(availableCharacteristics));
		}

		if (availableCharacteristics.length > 0) {
			setAvailableCharacteristics(availableCharacteristics);
			// Setting availableCharacteristics for priceSlider
			availableCharacteristics.forEach((item) => {
				if (item.title === "Price") {
					setPriceSliderValues((prevState) => ({
						...prevState,
						availableRange: [Math.min(...item.values), Math.max(...item.values)],
					}));
				}
			});
		}
	};

	const updateAvailableCharacteristics = async () => {
		setAvailableCharacteristics((prevState) => {
			return prevState.map((item) => {
				const itemsCount = {};

				for (let key in item.itemsCount) {
					itemsCount[key] = null;
				}

				return { ...item, itemsCount };
			});
		});
	};

	const loadMoreProducts = async (page, itemsPerPage) => {
		if (!isAppliedFilters) {
			const result = await getProducts(page, itemsPerPage);
			setProducts((prev) => [...prev, ...result]);
			setPages((prev) => ({
				...prev,
				currentPageNumber: page,
				productsPerPage: itemsPerPage,
			}));
		} else {
			// Getting a total list of products, which satisfy to filter parameters
			const products = await getFilteredData(checkedFilters, selectedSortOption);

			// Slicing products array for getting new portion of filtered products
			const result = products.slice((page - 1) * itemsPerPage, (page - 1) * itemsPerPage + itemsPerPage - 1);

			setProducts((prev) => [...prev, ...result]);
			setPages((prev) => ({
				...prev,
				currentPageNumber: page,
			}));
		}
	};

	const loadNewPage = async (page, itemsPerPage, checkedFiltersList, sortingCondition) => {
		setFirstLoad(true);
		setProducts([]);

		if (!isAppliedFilters) {
			const products = await getFilteredData([], sortingCondition);
			let result = products.slice((page - 1) * itemsPerPage, (page - 1) * itemsPerPage + itemsPerPage - 1);

			if (page === 1) {
				result = products.slice(0, itemsPerPage);
			}

			setProducts([...result]);
			setPages((prev) => ({
				...prev,
				currentPageNumber: page,
				availablePagesQuantity: Math.ceil(products.length / pages.productsPerPage),
			}));
		} else {
			// Getting checked filter array from searchParams
			const checkedFiltersArray = getURLSearchParams();

			// Getting a total list of products, which satisfy to filter parameters
			const products = await getFilteredData(checkedFiltersList || checkedFiltersArray, sortingCondition);

			// Slicing products array for getting new portion of filtered products
			let result = products.slice((page - 1) * itemsPerPage, (page - 1) * itemsPerPage + itemsPerPage - 1);

			if (page === 1) {
				result = products.slice(0, itemsPerPage);
			}

			setProducts([...result]);
			setPages((prev) => ({
				...prev,
				currentPageNumber: page,
				availablePagesQuantity: Math.ceil(products.length / pages.productsPerPage),
			}));
		}

		// Fixed showing message that products do not exist
		setTimeout(() => {
			setFirstLoad(false);
		}, 1000);
	};

	const onFirstLoad = async () => {
		getTotalCountOfPages();
		getAvailableCharacteristics();

		const sortingCondition = searchParams.get("sortBy");

		if (isAppliedFilters) {
			const checkedFilters = getURLSearchParams();
			handleURLSearchParams();
			updateAvailableCharacteristics();
			loadNewPage(
				pages.currentPageNumber,
				pages.productsPerPage,
				checkedFilters,
				sortingCondition ? sortingCondition : selectedSortOption
			);
		} else {
			loadNewPage(
				pages.currentPageNumber,
				pages.productsPerPage,
				[],
				sortingCondition ? sortingCondition : selectedSortOption
			);
		}
	};

	const openFilterAside = () => {
		setFilterAsideOpened(true);
		lockScroll();
	};

	const closeFilterAside = () => {
		setFilterAsideOpened(false);
		unlockScroll();
	};

	const getFilteredProducts = async (checkedFiltersArray, selectedSortOption) => {
		setFirstLoad(true);
		// Getting a total list of products, which satisfy to filter parameters
		const products = await getFilteredData(checkedFiltersArray, selectedSortOption);
		const filteredProductsQuantity = products.length;

		// Recalculating pages quantity and changing its state
		setPages((prev) => ({
			...prev,
			currentPageNumber: parseInt(searchParams.get("page")) || 1,
			availablePagesQuantity: Math.ceil(filteredProductsQuantity / prev.productsPerPage),
		}));

		// Decreasing the array of filtered products If there are more items then pages.productsPerPage
		if (products && filteredProductsQuantity > pages.productsPerPage) {
			products.length = pages.productsPerPage;
		}

		setProducts(products);
		setFirstLoad(false);
	};

	const onFiltersFormSumbit = async (event) => {
		if (event) {
			event.preventDefault();
		}

		// Reset products
		setProducts([]);
		setLoading(true);

		// Setting filter aside block state to "Loading"
		setFirstLoad(true);

		// Reset checkedFilters
		setCheckedFilters((prev) => {
			const result = prev.map((item) => ({ ...item, checkedItems: [] }));
			return result;
		});
		setActiveFiltersListVisible(false);

		const checkedFiltersArrayCopy = checkedFilters.map((filter) => ({ ...filter, checkedItems: [] }));

		const formData = [...new FormData(event.target)];

		if (formData.length > 0) {
			setActiveFiltersListVisible(true);

			// Forming an array of checked filters (checkedFiltersArrayCopy)
			formData.forEach((item) => {
				checkedFiltersArrayCopy.forEach((checkedFilter) => {
					if (item[0] === checkedFilter.title) {
						if (/\d+/i.test(item[1])) {
							if (!checkedFilter.checkedItems.includes(+item[1])) {
								checkedFilter.checkedItems.push(+item[1]);
							}
						} else {
							if (!checkedFilter.checkedItems.includes(item[1])) {
								checkedFilter.checkedItems.push(item[1]);
							}
						}
					}
				});
			});

			// Removing checkedFilters for "Price" if min&max prices are set
			checkedFiltersArrayCopy.forEach((checkedFilter) => {
				const minPrice = Math.min(
					...availableCharacteristics.filter((item) => item.title === "Price")[0].values
				);
				const maxPrice = Math.max(
					...availableCharacteristics.filter((item) => item.title === "Price")[0].values
				);

				if (checkedFilter.title === "Price") {
					if (JSON.stringify(checkedFilter.checkedItems) === JSON.stringify([minPrice, maxPrice])) {
						checkedFilter.checkedItems = [];
					}
				}
			});

			if (checkedFiltersArrayCopy.some((filter) => filter.checkedItems.length > 0)) {
				setAppliedFilters(true);

				// Call a function which transfers checkedFilters values to method for POST request and changes setProducts state
				getFilteredProducts(checkedFiltersArrayCopy, selectedSortOption);

				updateAvailableCharacteristics();
			} else {
				const pagesQuantity = await getPagesQuantity(pages.productsPerPage, false);

				getAvailableCharacteristics();

				setAppliedFilters(false);
				setPages((prev) => ({ ...prev, availablePagesQuantity: pagesQuantity }));
				loadNewPage(1, pages.productsPerPage, [], selectedSortOption);
				setTimeout(() => setFirstLoad(false), 1000);
			}

			// Setting searchParams for checkedFilters
			updateSearchParams(checkedFiltersArrayCopy, selectedSortOption);

			setCheckedFilters(checkedFiltersArrayCopy);

			closeFilterAside();

			// Deleting a page searchParam for the purpose page=1 on form submit
			searchParams.delete("page");
		}
	};

	const resetFilterList = () => {
		getAvailableCharacteristics();

		setCheckedFilters((prev) => {
			const result = [];
			prev.forEach((item) => result.push({ ...item, checkedItems: [] }));
			return result;
		});
		setAppliedFilters(false);
		setActiveFiltersListVisible(false);
		filterCatalogueFormRef.current.reset();
		resetPriceSlider();

		if (selectedSortOption === "By rating decrease") {
			setSearchParams({});
		} else {
			setSearchParams({ sortBy: selectedSortOption });
		}

		setPages((prev) => ({
			...prev,
			currentPageNumber: 1,
		}));

		loadNewPage(1, pages.productsPerPage, [], selectedSortOption);
	};

	const resetSelectedFilter = async (title) => {
		filterCatalogueFormRef.current.reset();
		setCheckedFilters((prev) => {
			const result = prev.map((filter) => {
				return filter.title === title ? { ...filter, checkedItems: [] } : { ...filter };
			});

			if (title === "Price") {
				resetPriceSlider();
			}

			return result;
		});

		const checkedFiltersArrayCopy = checkedFilters.map((filter) =>
			filter.title === title ? { ...filter, checkedItems: [] } : { ...filter }
		);
		updateSearchParams(checkedFiltersArrayCopy, selectedSortOption);

		if (checkedFiltersArrayCopy.every((filter) => filter.checkedItems.length === 0)) {
			getAvailableCharacteristics();
			setAppliedFilters(false);
		}

		setPages((prev) => ({
			...prev,
			currentPageNumber: 1,
		}));

		loadNewPage(1, pages.productsPerPage, checkedFiltersArrayCopy, selectedSortOption);
	};

	const updateSearchParams = (checkedFiltersArray, sortingCondition) => {
		const checkedItems = {};
		checkedFiltersArray.forEach((checkedFilter) => {
			if (checkedFilter.checkedItems.length !== 0) {
				checkedItems[checkedFilter.title] = checkedFilter.checkedItems.join(" ");
			}
		});

		if (sortingCondition === "By rating decrease") {
			setSearchParams(checkedItems);
		} else {
			setSearchParams({ ...checkedItems, sortBy: sortingCondition });
		}
	};

	const resetPriceSlider = () => {
		setPriceSliderValues((prevState) => ({
			...prevState,
			selectedRange: null,
		}));
	};

	const getURLSearchParams = () => {
		if ([...searchParams].length) {
			const params = Object.fromEntries(
				[...searchParams].map((param) => [
					param[0],
					param[1].split(" ").map((value) => (/\d+/i.test(value) ? +value : value)),
				])
			);

			const result = checkedFilters.map((item) => {
				if (params[item.title]) {
					return {
						...item,
						checkedItems: params[item.title],
					};
				}
				return {
					...item,
					checkedItems: [],
				};
			});

			return result;
		}
	};

	const handleURLSearchParams = () => {
		const checkedFilters = getURLSearchParams();

		if (checkedFilters) {
			setAppliedFilters(true);
			setActiveFiltersListVisible(true);
			setCheckedFilters(checkedFilters);

			checkedFilters.forEach((filter) => {
				if (filter.title === "Price" && filter.checkedItems.length === 2) {
					setPriceSliderValues((prevState) => ({
						...prevState,
						selectedRange: filter.checkedItems,
					}));
				}
			});
		}
	};

	const createPaginationItemLink = (pageNumberTo) => {
		const searchParamsString = searchParams.toString().replace(/page=\d+/gi, "");

		if (searchParamsString) {
			if (pageNumberTo === 1) {
				return `?${searchParamsString}`;
			}

			return `?${searchParamsString}&page=${pageNumberTo}`;
		} else {
			if (pageNumberTo === 1) {
				return "";
			}

			return `?page=${pageNumberTo}`;
		}
	};

	// Method for Select component (removing page from the URL and redirecting to page 1)
	const onChangeSortCondition = (sortingCondition) => {
		searchParams.delete("page");
		searchParams.delete("sortBy");

		const newSearchParams = Object.fromEntries([...searchParams]);

		if (sortingCondition) {
			setSearchParams({ ...newSearchParams, sortBy: sortingCondition });
		} else {
			setSearchParams(newSearchParams);
		}
	};

	return (
		<>
			<Helmet>
				<title>Catalogue - Smart Technics</title>
				<meta
					name="description"
					content={
						"You can find any gyroscooter by using available filters an sorting. You can also range products by some criterias"
					}
				/>
			</Helmet>
			<main>
				<div className="container">
					<div className="breadcrumbs">
						<ul className="breadcrumbs__list">
							<li className="breadcrumbs__item">
								<Link className="breadcrumbs__link" to="/">
									Home
								</Link>
							</li>
							<li className="breadcrumbs__item">
								<span className="breadcrumbs__link">Catalogue</span>
							</li>
						</ul>
					</div>
					<section className="page-catalogue">
						<h3 className="page-catalogue__title">Catalogue</h3>
						<div className="page-catalogue__body">
							<aside
								className={
									"page-catalogue__filter-body filter-catalogue" +
									(isFilterAsideOpened ? " opened" : "")
								}
							>
								<div className="filter-catalogue__header">
									<div className="filter-catalogue__title">Filters</div>
									<button
										className="filter-catalogue__btn"
										onClick={closeFilterAside}
										aria-label="show available filters to apply"
									></button>
								</div>
								{!isFirstLoad && availableCharacteristics.length > 0 ? (
									<form
										action="#"
										className="filter-catalogue__form"
										method="GET"
										ref={filterCatalogueFormRef}
										onSubmit={onFiltersFormSumbit}
									>
										<FilterItemBody
											title={`
                                                        ${
															availableCharacteristics.filter(
																(item) => item.title === "Price"
															)[0].title
														},
                                                        ${
															availableCharacteristics.filter(
																(item) => item.title === "Price"
															)[0].unit
														}
                                                    `}
											expanded
										>
											<PriceSlider value={priceSliderValues} setValue={setPriceSliderValues} />
										</FilterItemBody>
										{availableCharacteristics.map((characteristic) => {
											if (
												characteristic.title !== "Price" &&
												characteristic.title !== "Charging time"
											) {
												return (
													<FilterItemBody
														title={
															characteristic.unit
																? `${characteristic.title}, ${characteristic.unit}`
																: characteristic.title
														}
														key={characteristic.title}
														expanded={
															characteristic.title === "Engine power" ||
															checkedFilters.filter(
																(item) => item.title === characteristic.title
															)[0].checkedItems.length
														}
													>
														<FilterItemCharacteristicsBox
															title={characteristic.title}
															existingValues={characteristic.values || []}
															checkedValues={
																checkedFilters.filter(
																	(item) => item.title === characteristic.title
																)[0].checkedItems || []
															}
															itemsCount={characteristic.itemsCount}
														/>
													</FilterItemBody>
												);
											}
										})}
										<div className="filter-catalogue__item filter-item">
											<button className="filter-item__btn" type="submit">
												Apply
											</button>
										</div>
									</form>
								) : (
									<FilterCatalogueFormSkeleton />
								)}
							</aside>
							<Backdrop visible={isFilterAsideOpened} onClick={closeFilterAside} />
							<div className="page-catalogue__content-body content-catalogue">
								<div className="content-catalogue__header header-content-catalogue">
									<button className="header-content-catalogue__btn" onClick={openFilterAside}>
										<span>Filters</span>
									</button>
									<ActiveFiltersList
										checkedFilters={checkedFilters}
										animationSpeed={300}
										resetFilterList={resetFilterList}
										resetSelectedFilter={resetSelectedFilter}
										isVisible={isActiveFiltersListVisible}
									/>
									<div className="header-content-catalogue__select" id="select">
										<Select
											placeholder="Choose an option"
											selectedValue={selectedSortOption}
											options={["By rating decrease", "By price increase", "By price decrease"]}
											searchModule={false}
											updateSearchParams={onChangeSortCondition}
											setSelectedSortOption={setSelectedSortOption}
											productsPerPage={pages.productsPerPage}
											checkedFilters={checkedFilters}
											loadNewPage={loadNewPage}
										/>
									</div>
								</div>
								<div className="content-catalogue__products products-content-catalogue">
									<ul className="products-content-catalogue__all-cards">
										{products.length > 0
											? products.map((product) => (
													<ProductCard key={product.productId} {...product} />
											  ))
											: null}
										{loading ? <ProductsSectionSkeleton items={3} /> : null}
									</ul>
									{error ? <ProductsSectionError /> : null}
									{!loading && !error && products.length === 0 && !isFirstLoad && (
										<p className="products-content-catalogue__no-products">
											We haven't found any product that <br /> satisfies to your searching
											parameters.
										</p>
									)}
								</div>
								{!loading && !error && products.length !== 0 && (
									<div className="content-catalogue__actions user-catalogue-actions">
										{pages.currentPageNumber < pages.availablePagesQuantity && (
											<button
												className={
													loading
														? "user-catalogue-actions__more loading"
														: "user-catalogue-actions__more"
												}
												onClick={() =>
													loadMoreProducts(pages.currentPageNumber + 1, pages.productsPerPage)
												}
											>
												<span className="user-catalogue-actions__more-span">More</span>
												<svg
													width="24"
													height="24"
													viewBox="0 0 24 24"
													fill="none"
													xmlns="http://www.w3.org/2000/svg"
												>
													<path
														d="M12 2C10.0222 2 8.08879 2.58649 6.4443 3.6853C4.79981 4.78412 3.51809 6.3459 2.76121 8.17317C2.00433 10.0004 1.8063 12.0111 2.19215 13.9509C2.578 15.8907 3.53041 17.6725 4.92894 19.0711C6.32746 20.4696 8.10929 21.422 10.0491 21.8079C11.9889 22.1937 13.9996 21.9957 15.8268 21.2388C17.6541 20.4819 19.2159 19.2002 20.3147 17.5557C21.4135 15.9112 22 13.9778 22 12C22 10.6868 21.7413 9.38642 21.2388 8.17317C20.7363 6.95991 19.9997 5.85752 19.0711 4.92893C18.1425 4.00035 17.0401 3.26375 15.8268 2.7612C14.6136 2.25866 13.3132 2 12 2V2ZM12 20C10.4178 20 8.87104 19.5308 7.55544 18.6518C6.23985 17.7727 5.21447 16.5233 4.60897 15.0615C4.00347 13.5997 3.84504 11.9911 4.15372 10.4393C4.4624 8.88743 5.22433 7.46197 6.34315 6.34315C7.46197 5.22433 8.88743 4.4624 10.4393 4.15372C11.9911 3.84504 13.5997 4.00346 15.0615 4.60896C16.5233 5.21447 17.7727 6.23984 18.6518 7.55544C19.5308 8.87103 20 10.4177 20 12C20 14.1217 19.1572 16.1566 17.6569 17.6569C16.1566 19.1571 14.1217 20 12 20V20ZM16 11H13V8C13 7.73478 12.8946 7.48043 12.7071 7.29289C12.5196 7.10536 12.2652 7 12 7C11.7348 7 11.4804 7.10536 11.2929 7.29289C11.1054 7.48043 11 7.73478 11 8V11H8C7.73479 11 7.48043 11.1054 7.2929 11.2929C7.10536 11.4804 7 11.7348 7 12C7 12.2652 7.10536 12.5196 7.2929 12.7071C7.48043 12.8946 7.73479 13 8 13H11V16C11 16.2652 11.1054 16.5196 11.2929 16.7071C11.4804 16.8946 11.7348 17 12 17C12.2652 17 12.5196 16.8946 12.7071 16.7071C12.8946 16.5196 13 16.2652 13 16V13H16C16.2652 13 16.5196 12.8946 16.7071 12.7071C16.8946 12.5196 17 12.2652 17 12C17 11.7348 16.8946 11.4804 16.7071 11.2929C16.5196 11.1054 16.2652 11 16 11Z"
														fill="currentColor"
													/>
												</svg>
											</button>
										)}
										<div className="user-catalogue-actions__pagination pagination">
											{pages.availablePagesQuantity && (
												<Pagination
													count={pages.availablePagesQuantity}
													page={pages.currentPageNumber}
													variant="outlined"
													shape="rounded"
													color="primary"
													size="large"
													renderItem={(item) => (
														<PaginationItem
															component={Link}
															to={createPaginationItemLink(item.page)}
															{...item}
															onClick={() =>
																loadNewPage(
																	item.page,
																	pages.productsPerPage,
																	checkedFilters,
																	selectedSortOption
																)
															}
														/>
													)}
												/>
											)}
										</div>
									</div>
								)}
							</div>
						</div>
					</section>
				</div>
			</main>
		</>
	);
};

export default CataloguePage;
