import React from "react";

const FilterItemCharacteristicsBox = ({ title, existingValues, checkedValues, itemsCount }) => {
	return (
		<div className="filter-item__characteristics-box">
			{existingValues.length > 0 &&
				existingValues.map((value) => (
					<React.Fragment key={`${title}-${value}`}>
						<input
							className="filter-item__input"
							type="checkbox"
							id={`${title}-${value}`}
							value={value}
							name={title}
							disabled={itemsCount[value] === 0}
							defaultChecked={checkedValues.includes(value)}
						/>
						<label className={"filter-item__label"} htmlFor={`${title}-${value}`} tabIndex={0}>
							{value}
							{itemsCount[value] ? <span>({itemsCount[value]})</span> : null}
						</label>
					</React.Fragment>
				))}
		</div>
	);
};

export default FilterItemCharacteristicsBox;
